.Banner{
    background-size: cover;
    height: 440px;
    color: white;
}
.content{
    padding-top: 140px;
    height: 190px;
}
.title{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
    padding-left: 15px;

}

.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(51, 51, 51,.5);
    cursor: pointer;
    margin-right: 1rem;
    padding-left: 15px;

}
.banner_buttons{
    padding-left: 15px;

}
.button:hover{
color: black;
background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 25rem;
    padding-left: 15px;

}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}